body {
  margin: 0px;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 0px;
  text-align: left;
  background-color: rgba(242, 241, 239, 1);
}
h1 {
  font-size: 40px;
}
p {
  color: rgba(173, 172, 187, 1);
  font-size: 16px;
}
a {
  color: rgba(242, 241, 239, 1);
  text-decoration: none;
}
a:hover {
  color: white;
}
#banner_canvas {
  width: 2000px;
  height: 137px;
  background-image: url("../images/whypam_banner.png");
}
#menu_container {
  width: 2000px;
  height: 58px;
  font-size: 22px;
  color: rgba(255, 255, 255, 1);
  background-color: rgba(174, 194, 107, 1);
}
#portfolio_table {
  color: rgba(174, 194, 107, 1);
  font-size: 25px;
  text-align: left;
  border-collapse: collapse;
}
.portfolio_title {
  color: rgba(239, 145, 146, 1);
  font-size: 28px;
  text-align: center;
  padding: 0px 300px 0px 0px;
}
.site_title {
  color: rgba(174, 194, 107, 1);
  font-size: 30px;
}
.image_align {
  text-align: center;
}
[class*="col-"] {
  float: left;
  /*padding: 5px 0px 5px 0px;*/
  padding: 20px 0px 0px 0px;
}
.col-1 {
  width: 25%;
}
.col-1a {
  width: 18%;
}
.col-2 {
  width: 7px;
  height: 9200px;
  background-color: rgba(174, 194, 107, 1);
}
.col-2a {
  width: 7px;
}
.col-3 {
  width: 72%;
  float: right;
}
.col-3a {
  width: 80%;
}
.menu {
  color: rgba(242, 241, 239, 1);
  font-size: 16px;
  font-weight: bold;
  padding: 0px 50px 0px 0px;
}
.caption {
  color: rgba(174, 194, 107, 1);
  font-size: 18px;
  text-align: left;
  padding: 0px 0px 0px 40px;
}
.copyright {
  color: rgba(174, 194, 107, 1);
  font-size: 18px;
  text-align: right;
  padding: 0px 10px 0px 0px;
}
